export default {
  //resolve => require(["./slot/MyInfo.vue"], resolve),
  components: {
    MyReadCoin: resolve => require(["./slot/MyReadCoin.vue"], resolve), //阅读币组件
    MySendCoin: resolve => require(["./slot/MySendCoin.vue"], resolve) //赠币组件
  },
  data() {
    return {
      coinShow: "阅读币"
    };
  },
  methods: {
    //接受路由参数
    get_router() {
      this.coinShow = this.$route.query.id;
    }
  },
  created() {},
  mounted() {
    this.get_router();
  }
};
